<template>
	<router-view v-if="isDataLoaded"></router-view>
</template>

<script>
import Vue from 'vue';
import TuiGrid from 'tui-grid';
import merge from 'lodash/merge';
import { mapMutations, mapState } from 'vuex';
import {
	escapeHtml,
	HttpReq,
	logout,
	MenuClickEventBus,
	ModalKeyupEventBus,
	validateOpenNewTabUrl,
	validateToken,
	WindowResizeEventBus
} from '@/utils/common.js';

export default {
	name: 'App',
	data() {
		return {
			isDataLoaded: false,
			userInfoReq: new HttpReq('/api/page/portal'),
			messageReq: new HttpReq(`/api/page/messages`),
			setInfoReq: new HttpReq(`/api/page/config`),
			titleReq: new HttpReq(`/api/page/title`),
			publishServiceReq: new HttpReq(`/api/page/isPublishService`)
		};
	},
	computed: {
		...mapState(['constantsInfo', 'userInfo', 'regExpList', 'windowId', 'modalCloseKeyCode', 'isRefreshMode'])
	},
	beforeCreate() {
		// Vue
		Vue.config.productionTip = false;
		Vue.config.devtools = false;

		// TuiGrid
		TuiGrid.setLanguage('ko', {
			display: {
				noData: this.$t(`msg.noData`)
			}
		});

		TuiGrid.applyTheme('clean', {
			row: {
				hover: {
					background: '#f5f6fb'
				}
			}
		});

		//ie11 > tui-grid에서 트리를 열고 닫을때 오류. 임시로 함.
		const agent = navigator.userAgent.toLowerCase();

		if ((navigator.appName == 'Netscape' && -1 != agent.indexOf('trident')) || -1 != agent.indexOf('msie')) {
			document.createTreeWalker = () => {
				return {
					nextNode() {
						return false;
					}
				};
			};
		}

		//ajaxSetup
		$.ajaxSetup({
			type: `POST`,
			xhrFields: {
				withCredentials: true
			},
			beforeSend: function (xhr) {
				xhr.setRequestHeader('Authorization', localStorage.getItem('accessToken'));
				xhr.setRequestHeader('X-iManager-Method', `GET`);
			}
		});

		if (typeof window.CustomEvent !== `function`) {
			const CustomEvent = function (event, params) {
				params = params || { bubbles: false, cancelable: false, detail: undefined };
				const evt = document.createEvent('CustomEvent');
				evt.initCustomEvent(event, params.bubbles, params.cancelable, params.detail);
				return evt;
			};

			CustomEvent.prototype = window.Event.prototype;

			window.CustomEvent = CustomEvent;
		}

		//validateOpenNewTabUrl
		window.validateOpenNewTabUrl = validateOpenNewTabUrl;
	},
	async created() {
		this.isDataLoaded = false;

		const titleResult = await this.titleReq.read();

		document.title = escapeHtml(titleResult.data.object);

		if (!(await validateToken()).result) {
			if (localStorage.getItem(`accessToken`)) {
				this.$alert({
					type: `warn`,
					backdropMode: `full`,
					message: this.$t(`msg.validateToken`),
					callback: async () => {
						MenuClickEventBus.$emit(`ModalDelete`);

						logout();

						this.isDataLoaded = true;
					}
				});
			} else {
				logout();

				this.isDataLoaded = true;
			}
		} else {
			if (this.userInfo) {
				localStorage.setItem(`motionTime`, Date.now());

				this.initWindowEvt();
				await this.initMessage();
				await this.initSettingInfo();
				this.isDataLoaded = true;

				this.$router.push({ name: sessionStorage.getItem('externalMenuUrl') ? `layoutExternal` : `layout` }).catch(() => {});
			} else {
				const userInfoReqResult = await this.userInfoReq.read({ withPrivilege: true });

				if (`ok` === userInfoReqResult.data.result) {
					localStorage.setItem(`motionTime`, Date.now());

					this.initWindowEvt();
					await this.initMessage();
					await this.initSettingInfo();

					const userInfo = userInfoReqResult.data.object.user;
					const totalUserPrivileges = userInfo.totalUserPrivileges;

					if (0 < totalUserPrivileges.filter(userPrivilegeInfo => userPrivilegeInfo.admin).length) {
						if (!totalUserPrivileges.find(userPrivilegeInfo => `UserViewer` === userPrivilegeInfo.privilegeId)) {
							totalUserPrivileges.push({
								admin: false,
								member: true,
								privilegeDesc: `User Viewer`,
								privilegeId: `UserViewer`,
								privilegeType: `S`,
								readOnly: false,
								role: false,
								roleType: `p`,
								onlyFrontPrivilege: true
							});
						}

						if (!totalUserPrivileges.find(userPrivilegeInfo => `UserEditor` === userPrivilegeInfo.privilegeId)) {
							totalUserPrivileges.push({
								admin: false,
								member: true,
								privilegeDesc: `User Editor`,
								privilegeId: `UserEditor`,
								privilegeType: `S`,
								readOnly: false,
								role: false,
								roleType: `p`,
								onlyFrontPrivilege: true
							});
						}
					}

					this.setUserInfo(userInfo);

					this.setMenuInfo(userInfoReqResult.data.object.menu);

					this.setHomeMenu(userInfoReqResult.data.object.home);

					this.isDataLoaded = true;

					this.$router.push({ name: sessionStorage.getItem('externalMenuUrl') ? `layoutExternal` : `layout` }).catch(() => {});
				} else {
					logout();

					this.isDataLoaded = true;
				}
			}
		}
	},
	methods: {
		...mapMutations([
			'setUserInfo',
			'setHomeMenu',
			'setMenuInfo',
			'setIsOnPopState',
			'setRegExpList',
			'setModalCloseKeyCode',
			`setIsUseTheme`,
			`setDetailConstants`,
			`setConstantsInfo`,
			`setAutoLogoutInterval`
		]),
		initWindowEvt() {
			// message
			window.$t = this.$t;
			window.$t = window.$t.bind(this);

			// resize
			window.onresize = evt => {
				WindowResizeEventBus.$emit('MainResize');

				document.body.dispatchEvent(
					new CustomEvent('resize', {
						detail: {
							evt
						}
					})
				);
			};

			// onpopstate
			window.onpopstate = event => {
				if (!event.state || !event.state.menuId) return;

				this.setIsOnPopState(true);

				document.getElementById(event.state.menuId).click();
			};

			// alertModalCloseKeyupEvt
			const modalCloseKeyUpEvt = e => {
				if (!this.modalCloseKeyCode.some(keyCode => keyCode === e.keyCode) || e.code === 'Space') return;

				this.$nextTick(() => {
					document.querySelectorAll(`.modal`).forEach(element => {
						if (`block` !== element.style.display) return;

						if (element.classList.contains(`fade`)) {
							$(element).modal('hide');
						} else {
							ModalKeyupEventBus.$emit('ModalKeyup');
						}
					});
				});
			};

			if (window.modalCloseKeyUpEvt) {
				document.body.removeEventListener(`keyup`, window.modalCloseKeyUpEvt);
			}

			window.modalCloseKeyUpEvt = modalCloseKeyUpEvt;

			document.body.addEventListener(`keyup`, window.modalCloseKeyUpEvt);
		},
		async initMessage() {
			// locale
			let locale = navigator.language || navigator.userLanguage;
			locale = locale.toLowerCase();
			locale = locale.substring(0, 2);

			this.$i18n.locale = locale;

			// message
			const result = await this.messageReq.read({ locale });

			if (`ok` !== result.data.result) return;

			this.$i18n.setLocaleMessage(this.$i18n.locale, merge(this.$i18n.getLocaleMessage(locale), result.data.object));
		},
		async initSettingInfo() {
			let result = await this.setInfoReq.read({ menuUrl: `/constants` });

			if (`ok` !== result.data.result) return;

			const constantsInfo = new Function(`${result.data.object} return constants;`)();

			this.setRegExpList(constantsInfo.regExpList);

			this.setModalCloseKeyCode(constantsInfo.modalCloseKeyCode);

			this.setIsUseTheme(constantsInfo.isUseTheme ? true : false);

			this.setDetailConstants(constantsInfo.detail);

			result = await this.publishServiceReq.read();

			constantsInfo.isPublishService = result.data.object;

			this.setConstantsInfo(constantsInfo);

			if (0 < constantsInfo.motionDetectTime) {
				// checkMotionFunc
				const checkMotionFunc = () => {
					if (localStorage.getItem(`noAutoLogout`)) return true;

					const motionTime = localStorage.getItem(`motionTime`);

					if (null === motionTime || constantsInfo.autoLogoutTime < Date.now() - motionTime) {
						MenuClickEventBus.$emit(`ModalDelete`);

						logout();

						if (null !== motionTime) {
							window.$alert({
								type: `warn`,
								backdropMode: `full`,
								message: this.$t(`msg.no.motion.logout.warn`)
							});
						}

						return false;
					} else {
						return true;
					}
				};
				// checkMotionFunc

				// checkMotionFunc webWorker
				const onmessage = function (e) {
					const { motionDetectTime, refreshToken } = e.data;

					setInterval(() => {
						postMessage({ refreshToken });
					}, motionDetectTime);
				};

				const intervalWorker = new Worker(
					URL.createObjectURL(
						new Blob([`self.onmessage = ${onmessage.toString()}`], {
							type: `application/javascript`
						})
					)
				);

				intervalWorker.postMessage({
					motionDetectTime: constantsInfo.motionDetectTime,
					refreshToken: localStorage.getItem(`refreshToken`)
				});

				intervalWorker.onmessage = e => {
					if (!localStorage.getItem(`refreshToken`)) {
						intervalWorker.terminate();
						logout();
					} else if (checkMotionFunc()) {
						if (localStorage.getItem(`refreshToken`) !== e.data.refreshToken) intervalWorker.terminate();
					} else {
						intervalWorker.terminate();
					}
				};
				// checkMotionFunc webWorker

				// checkMotionFunc checkActiveTabFunc
				window.checkActiveTabEventFunc = () => {
					if (document.hidden) return;

					if (this.isRefreshMode || window.isRefreshMode) localStorage.setItem(`noAutoLogout`, true);
					else localStorage.removeItem('noAutoLogout');
				};

				window.removeEventListener(`visibilitychange`, window.checkActiveTabEventFunc);
				window.addEventListener(`visibilitychange`, window.checkActiveTabEventFunc);
				// checkMotionFunc checkActiveTabFunc

				// checkMotionFunc checkMotionEventFunc
				window.checkMotionEventFunc = () => {
					if (!checkMotionFunc()) return;
					localStorage.setItem(`motionTime`, Date.now());
				};

				document.body.removeEventListener(`keyup`, window.checkMotionEventFunc);
				document.body.removeEventListener(`mouseup`, window.checkMotionEventFunc);
				document.body.removeEventListener(`mousemove`, window.checkMotionEventFunc);

				document.body.addEventListener(`keyup`, window.checkMotionEventFunc);
				document.body.addEventListener(`mouseup`, window.checkMotionEventFunc);
				document.body.addEventListener(`mousemove`, window.checkMotionEventFunc);
				// checkMotionFunc checkMotionEventFunc
			}

			if (constantsInfo.isUseTheme) {
				if (localStorage.getItem(`theme`) && `moon` === localStorage.getItem(`theme`)) {
					document.body.classList.add(`moon`);
				}
			} else {
				localStorage.removeItem(`theme`);
				document.body.classList.remove(`moon`);
			}
		}
	}
};
</script>
