import Vue from 'vue';
import VueRouter from 'vue-router';
import state from '@/store/state';
import mutations from '@/store/mutations';

Vue.use(VueRouter);

import App from '@/App';
import Login from '@/views/Login';
import Layout from '@/views/Layout';

const router = new VueRouter({
	mode: 'history',
	base: process.env.BASE_URL,
	routes: [
		{ path: '/app/login', name: 'login', component: Login },
		{ path: '/app/layout', name: 'layout', component: Layout },
		{ path: '/', name: 'app', component: App }
	]
});

export default router;

router.beforeEach((to, from, next) => {
	if (to.path.startsWith(`/app/login`)) {
		if (to.query && 0 < Object.keys(to.query).length) {
			if (!localStorage.getItem(`accessToken`)) mutations.setLoginQueryParam(state, JSON.stringify(to.query));
		}
	}

	next();
});
