<template>
	<div @click="gridFocusOut">
		<ul class="collapse-list collapse-list-result m-0">
			<li id="mappingRuleTitle" class="collapse-item">
				<button
					type="button"
					class="collapse-link"
					data-toggle="collapse"
					data-target="#mappingRule"
					ref="mappingRuleTitleBtn"
					@click="mappingTitleBtnClick"
				>
					<span>{{ $t('label.mapping.rule') }}</span>
				</button>
				<div class="collapse show" id="mappingRule">
					<div class="card card-body">
						<div class="form-inline m-full" v-if="!this.data.readonly">
							<button type="button" class="btn-icon" @click="addRule" :title="$t('label.mapping.rule.add')">
								<i class="icon-plus-circle"></i>
							</button>
							<button type="button" class="btn-icon" @click="removeRule" :title="$t('label.mapping.rule.remove')">
								<i class="icon-minus-circle"></i>
							</button>
						</div>
						<div ref="mappingRuleGridParent" class="table-responsive">
							<TuiGrid ref="mappingRuleGrid" v-bind="mappingRuleOptions" @click="ruleClick($event.rowKey)" />
						</div>
					</div>
				</div>
			</li>
			<li class="collapse-item">
				<button type="button" class="collapse-link" data-toggle="collapse" data-target="#mappingInfo" @click="mappingTitleBtnClick">
					<span>{{ $t('label.model.info') }}</span>
				</button>
				<div class="collapse show" id="mappingInfo">
					<div class="card card-body">
						<div class="form-inline m-full" v-if="!this.data.readonly">
							<button type="button" class="btn-icon" @click="initMappingInfo" :title="$t('label.initialize')">
								<i class="icon-reset"></i>
							</button>
							<button type="button" class="btn-icon" @click="getSameIdInfo" :title="$t('label.same.id.link')">
								<i class="icon-deal"></i>
							</button>
							<button
								type="button"
								class="btn-icon"
								ref="targetGridBtn"
								@click="removeSingleMapping"
								:title="$t('label.delete.single.mapping')"
							>
								<img id="icon-scissors" :src="getMenuIconUrl(`scissors`)" class="center-block" />
							</button>
						</div>
						<div ref="mappingGridArea" :style="style">
							<div ref="sourceGridParent" class="table-responsive">
								<TuiGrid ref="sourceGrid" v-bind="sourceModelOptions" />
							</div>
							<div ref="mappingArrowParent" class="table-responsive" @wheel="mappingArrowParentWheel">
								<canvas ref="mappingArrow"></canvas>
							</div>
							<div ref="targetGridParent" class="table-responsive">
								<TuiGrid ref="targetGrid" v-bind="targetModelOptions" />
							</div>
						</div>
					</div>
				</div>
			</li>
		</ul>
	</div>
</template>

<script>
import { getHttpUrl, WindowResizeEventBus, SideMenuToggleEventBus, makeGridOptions, escapeHtml, HttpReq } from '@/utils/common.js';
import { Grid } from '@toast-ui/vue-grid';
import cloneDeep from 'lodash/cloneDeep';

export default {
	props: {
		data: Object
	},
	name: 'MappingInfo',
	components: {
		TuiGrid: Grid
	},
	data() {
		return {
			dragger: null,
			errorRuleList: [],
			mappingRuleGridSelectedRowKey: null,
			style: {
				height: null,
				'overflow-y': `hidden`
			},
			sourceScrollTop: 0,
			targetScrollTop: 0,
			mappingRuleOptions: {
				draggable: !this.data.readonly,
				columns: [
					{
						name: 'sourceRecordObject',
						hidden: true
					},
					{
						name: 'targetRecordObject',
						hidden: true
					},
					{
						name: 'pk.mappingId',
						hidden: true
					},
					{
						name: 'pk.mappingOrder',
						hidden: true
					},
					{
						name: 'ruleDetail',
						hidden: true
					},
					{
						name: `multiFuncArrow`,
						hidden: true
					},
					{
						name: 'pk',
						header: 'Mapping ID&Order',
						formatter: value => {
							return `${value.row['pk.mappingId']}$${value.row['pk.mappingOrder']}`;
						}
					},
					{
						name: 'targetForm',
						header: 'Target Form',
						editor: 'text'
					},
					{
						name: 'mappingDetailDesc',
						header: this.$t('label.desc'),
						editor: 'text'
					}
				],
				data: [],
				minBodyHeight: '55',
				bodyHeight: '120',
				editingEvent: `click`,
				onGridMounted: evt => {
					evt.instance.on('click', clickEvt => {
						if (this.data.readonly) clickEvt.stop();
					});

					evt.instance.on('drop', () => {
						evt.instance.getData().forEach((info, idx) => {
							evt.instance.setValue(info.rowKey, 'pk.mappingOrder', idx);
						});
					});
				}
			},
			sourceModelOptions: {
				treeColumnOptions: {
					name: 'pk.fieldId'
				},
				scrollY: true,
				rowHeaders: [`rowNum`],
				columns: [
					{
						name: 'id',
						hidden: true
					},
					{
						name: 'fieldType',
						hidden: true
					},
					{
						name: 'pk.fieldId',
						header: 'Field',
						escapeHTML: true,
						formatter: ({ row }) => {
							let rtnFieldType = ``;

							if ('R' === row.fieldType) {
								if (row.recordObject) {
									rtnFieldType += `Group(${row.recordObject.recordType})`;
									if ('N' != row.arrayType) rtnFieldType += `[${row.arrayType}]`;
								}
							} else {
								const fieldTypeTranslateObj = {
									B: 'Byte',
									S: 'Short',
									I: 'Int',
									L: 'Long',
									F: 'Float',
									D: 'Double',
									T: 'String',
									N: 'Numeric',
									P: 'Decimal',
									A: 'Raw',
									p: 'Timestamp',
									b: 'Boolean',
									R: 'Record',
									v: 'Individual'
								};

								rtnFieldType += `${fieldTypeTranslateObj[row.fieldType]}(${row.fieldLength}${
									row.fieldScale ? `, ${row.fieldScale}` : ``
								})`;

								if ('N' != row.arrayType) rtnFieldType += `[${row.arrayType}]`;
							}

							return escapeHtml('R' === row.fieldType ? row['pk.fieldId'] : `${row['pk.fieldId']}(${row.fieldName}) ${rtnFieldType}`);
						}
					}
				],
				data: [],
				onGridMounted: evt => {
					evt.instance.on('expand', () => {
						setTimeout(() => {
							this.resize();
						}, 0);
					});

					evt.instance.on('collapse', evt => {
						evt.instance
							.getChildRows(evt.rowKey)
							.filter(row => `R` === row.fieldType)
							.forEach(row => {
								evt.instance.collapse(row.rowKey);
							});

						setTimeout(() => {
							this.resize();
						}, 0);
					});

					evt.instance.on(`click`, evt => {
						evt.nativeEvent.stopPropagation();

						if (`cell` !== evt.targetType) return;

						if (this.readonly) {
							this.delMultiSelect(`source`);
							this.isShiftKey = false;
						} else {
							this.delMultiSelect(`target`, true);
							this.clickMappingField(evt, `source`);
						}
					});

					evt.instance.gridEl.querySelector(`.tui-grid-rside-area .tui-grid-body-area`).addEventListener('scroll', evt => {
						this.sourceScrollTop = evt.target.scrollTop;

						setTimeout(() => {
							this.drawArrow();
						}, 0);
					});
				},
				onGridUpdated: () => {
					this.resize();
					this.initDragDropEvt();
				}
			},
			targetModelOptions: {
				treeColumnOptions: {
					name: 'pk.fieldId'
				},
				scrollY: true,
				rowHeaders: [`rowNum`],
				columns: [
					{
						name: 'id',
						hidden: true
					},
					{
						name: 'fieldType',
						hidden: true
					},
					{
						name: 'pk.fieldId',
						header: 'Field',
						escapeHTML: true,
						formatter: ({ row }) => {
							let rtnFieldType = ``;

							if ('R' === row.fieldType) {
								if (row.recordObject) {
									rtnFieldType += `Group(${row.recordObject.recordType})`;
									if ('N' != row.arrayType) rtnFieldType += `[${row.arrayType}]`;
								}
							} else {
								const fieldTypeTranslateObj = {
									B: 'Byte',
									S: 'Short',
									I: 'Int',
									L: 'Long',
									F: 'Float',
									D: 'Double',
									T: 'String',
									N: 'Numeric',
									P: 'Decimal',
									A: 'Raw',
									p: 'Timestamp',
									b: 'Boolean',
									R: 'Record',
									v: 'Individual'
								};

								rtnFieldType += `${fieldTypeTranslateObj[row.fieldType]}(${row.fieldLength}${
									row.fieldScale ? `, ${row.fieldScale}` : ``
								})`;

								if ('N' != row.arrayType) rtnFieldType += `[${row.arrayType}]`;
							}

							return 'R' === row.fieldType ? row['pk.fieldId'] : `${row['pk.fieldId']}(${row.fieldName}) ${rtnFieldType}`;
						}
					}
				],
				data: [],
				onGridMounted: evt => {
					evt.instance.on('expand', () => {
						setTimeout(() => {
							this.resize();
						}, 0);
					});

					evt.instance.on('collapse', evt => {
						evt.instance
							.getChildRows(evt.rowKey)
							.filter(row => `R` === row.fieldType)
							.forEach(row => {
								evt.instance.collapse(row.rowKey);
							});

						setTimeout(() => {
							this.resize();
						}, 0);
					});

					evt.instance.on(`click`, evt => {
						evt.nativeEvent.stopPropagation();

						if (`cell` !== evt.targetType) return;

						if (this.readonly) {
							this.delMultiSelect(`target`);
							this.isShiftKey = false;
						} else {
							this.delMultiSelect(`source`, true);

							if (`R` === evt.instance.getRow(evt.rowKey).fieldType) {
								evt.instance.blur();
								evt.instance.cancelEditing();
								return;
							}

							this.clickMappingField(evt, `target`);
						}
					});

					evt.instance.on(`dblclick`, evt => {
						if (`cell` !== evt.targetType) return;

						this.delMultiSelect(`target`);
						evt.instance.focus(evt.rowKey);

						const clickFieldInfo = evt.instance.getRow(evt.rowKey);

						if (`R` === clickFieldInfo.fieldType) return;

						const { ruleDetail, sourceRecordObject, targetRecordObject } = this.$refs.mappingRuleGrid.invoke(
							`getRow`,
							this.mappingRuleGridSelectedRowKey
						);

						const ruleDetailInfo = ruleDetail ? ruleDetail.find(({ targetFieldPath }) => targetFieldPath === clickFieldInfo.id) : null;

						const mappingExpression = ruleDetailInfo ? ruleDetailInfo.mappingExpression : null;
						const arraySizeParameter = ruleDetailInfo ? ruleDetailInfo.arraySizeParameter : null;

						const _this = this;

						const modalInstance = this.$openModal({
							title: `${this.$t('label.mapping.rule.detail')}`,
							contentList: [
								{
									template: `<mappingRuleDetail :data="data" :readonly="readonly" />`,
									components: {
										mappingRuleDetail: require('@/views/modal/mapping/ruleDetail/MappingRuleDetail.vue').default
									},
									data() {
										return {
											data: {
												clickFieldInfo,
												mappingExpression,
												arraySizeParameter,
												sourceRecordIdArr: sourceRecordObject,
												targetRecordIdArr: targetRecordObject,
												fieldKey: evt.rowKey
											},
											readonly: _this.data.readonly
										};
									}
								}
							],
							buttonList: !_this.data.readonly
								? [
										{
											template: `<button type="button" class="btn btn-primary" @click="click"> 
													    ${this.$t(`label.ok`)} 
													   </button>`,
											methods: {
												async click() {
													const { mappingExpression, arrayReference } =
														modalInstance.$refs.content[0].$children[0].getData();

													if (!mappingExpression) {
														this.$alert({ type: 'warn', message: this.$t('msg.no.data') });

														return;
													}

													const adjustRuleDetail =
														null === ruleDetail
															? []
															: ruleDetail
																	.filter(rule => rule.targetFieldPath !== clickFieldInfo.id)
																	.map(info => ({
																		targetFieldPath: info.targetFieldPath,
																		mappingExpression: info.mappingExpression,
																		simpleMappingYn: info.simpleMappingYn,
																		arraySizeParameter: info.arraySizeParameter
																	}));

													let simpleMappingYn = `Y`;

													if (mappingExpression) {
														const {
															data: {
																object: { type }
															}
														} = await new HttpReq(`/api/mapping/expression`).read({ expression: mappingExpression });

														if (`ConstantExpression` !== type && `RecordExpression` !== type) {
															simpleMappingYn = `N`;
														}
													}

													adjustRuleDetail.push({
														targetFieldPath: clickFieldInfo.id,
														mappingExpression,
														simpleMappingYn,
														arraySizeParameter: arrayReference
													});

													_this.$refs.mappingRuleGrid.invoke(
														'setValue',
														_this.mappingRuleGridSelectedRowKey,
														'ruleDetail',
														adjustRuleDetail
													);

													await _this.initMultiFuncArrow(_this.mappingRuleGridSelectedRowKey);

													_this.resize();

													modalInstance.hideModal();
												}
											}
										}
								  ]
								: null
						});
					});

					evt.instance.gridEl.querySelector(`.tui-grid-rside-area .tui-grid-body-area`).addEventListener('scroll', evt => {
						this.targetScrollTop = evt.target.scrollTop;

						setTimeout(() => {
							this.drawArrow();
						}, 0);
					});
				},
				onGridUpdated: () => {
					this.resize();
					this.initDragDropEvt();
				}
			},

			multiSourceFields: [],
			multiTargetFields: [],
			isShiftKey: false
		};
	},
	created() {
		this.mappingRuleOptions = makeGridOptions(this.mappingRuleOptions);
		this.sourceModelOptions = makeGridOptions(this.sourceModelOptions);
		this.targetModelOptions = makeGridOptions(this.targetModelOptions);

		WindowResizeEventBus.$on('MainResize', this.mainResize);
		SideMenuToggleEventBus.$on('MainResize', this.mainResize);
	},
	async mounted() {
		this.mainResize();

		if (this.data.mappingObject) {
			this.data.mappingObject.mappingDetails.forEach(mappingDetail => {
				const sourceRecordObject = `I` === this.data.type ? this.data.interfaceRecordInfo : this.data.serviceRecordInfo;
				let targetRecordObject = `I` === this.data.type ? this.data.serviceRecordInfo[0] : this.data.interfaceRecordInfo[0];

				if (`I` === this.data.type) {
					targetRecordObject = this.data.serviceRecordInfo[0];
				} else {
					targetRecordObject = this.data.interfaceRecordInfo.find(info => info.id === mappingDetail.mappingRuleObject.recordId);

					if (!targetRecordObject) {
						targetRecordObject = this.data.interfaceRecordInfo[0];
					}
				}

				this.$refs.mappingRuleGrid.invoke('appendRow', {
					'pk.mappingId': mappingDetail.pk.mappingId,
					'pk.mappingOrder': mappingDetail.pk.mappingOrder,
					targetForm: mappingDetail.targetForm,
					mappingDetailDesc: mappingDetail.mappingDetailDesc,
					sourceRecordObject,
					targetRecordObject,
					ruleDetail: mappingDetail.mappingRuleObject.mappingRuleDetails.map(mappingRuleDetail => ({
						mappingExpression: mappingRuleDetail.mappingExpression,
						targetFieldPath: mappingRuleDetail.targetFieldPath,
						simpleMappingYn: mappingRuleDetail.simpleMappingYn,
						arraySizeParameter: mappingRuleDetail.arraySizeParameter
					}))
				});
			});

			this.$nextTick(async () => {
				if (0 === this.$refs.mappingRuleGrid.invoke('getRowCount')) return;

				await this.initMultiFuncArrowAll();

				this.mappingRuleGridSelectedRowKey = this.$refs.mappingRuleGrid.invoke('getRowAt', 0).rowKey;

				this.ruleClick(this.mappingRuleGridSelectedRowKey);

				this.checkRule();

				this.resize();
			});
		}
	},
	methods: {
		getMenuIconUrl(iconName) {
			return getHttpUrl(`/custom/img/${iconName}.svg`);
		},
		checkRule() {
			this.errorRuleList = [];

			const mappingRule = this.$refs.mappingRuleGrid.invoke('getData');

			for (let i = 0; i < mappingRule.length; i++) {
				const mappingRuleInfo = mappingRule[i];

				const multiFuncArrow = mappingRuleInfo.multiFuncArrow;

				const sourceRecordObject = mappingRuleInfo.sourceRecordObject;
				const targetRecordObject = mappingRuleInfo.targetRecordObject;

				const sourceIdList = [];
				const targetIdList = [];

				sourceRecordObject.forEach((recordObject, idx) => {
					this.extractionFieldId(sourceIdList, recordObject.fields, `$${idx}`);
				});

				this.extractionFieldId(targetIdList, targetRecordObject.fields, ``);

				mappingRuleInfo.ruleDetail.forEach(detail => {
					let isTargetError = false;
					let isSourceError = false;

					const targetFieldPath = detail.targetFieldPath;
					const mappingExpression = detail.mappingExpression;

					isTargetError = targetIdList.find(id => id === targetFieldPath) ? false : true;

					if (multiFuncArrow && 0 < multiFuncArrow.filter(multiFunc => multiFunc.targetFieldPath === targetFieldPath).length) {
						const list = multiFuncArrow.filter(multiFunc => multiFunc.targetFieldPath === targetFieldPath);

						let findMappingExpression = true;

						for (let i = 0; i < list.length; i++) {
							if (!sourceIdList.find(id => id === list[i].mappingExpression)) {
								findMappingExpression = false;
								break;
							}
						}

						isSourceError = findMappingExpression ? false : true;
					} else {
						if (mappingExpression.startsWith(`$${i}\\`)) {
							isSourceError = sourceIdList.find(id => id === mappingExpression) ? false : true;
						} else {
							isSourceError = false;
						}
					}

					if (isTargetError || isSourceError) {
						this.errorRuleList.push({
							order: i,
							mappingExpression,
							targetFieldPath,
							errorTarget: isTargetError && isSourceError ? `both` : isTargetError ? `target` : `source`
						});
					}
				});
			}

			if (0 < this.errorRuleList.length) this.openErrorRuleModal();
		},
		openErrorRuleModal() {
			const _this = this;

			const mappingRule = this.$refs.mappingRuleGrid.invoke('getData');

			const targetErrorRuleList = this.errorRuleList.filter(({ errorTarget }) => `both` === errorTarget || `target` === errorTarget);

			for (let i = 0; i < mappingRule.length; i++) {
				const ruleDetail = mappingRule[i].ruleDetail;

				const validateRuleList = ruleDetail.filter(rule => {
					return targetErrorRuleList.find(({ targetFieldPath }) => targetFieldPath === rule.targetFieldPath) ? false : true;
				});

				this.$refs.mappingRuleGrid.invoke('setValue', mappingRule[i].rowKey, 'ruleDetail', validateRuleList);
			}

			this.$openModal({
				title: `${this.$t('label.mapping')} ${this.$t('label.error.info')}`,
				modalWidth: `1280px`,
				modalHeight: `500px`,
				contentList: [
					{
						template: `<div>
									<div class="table-responsive">
										<TuiGrid ref="tuiGrid" v-bind="options" />
									</div>
							   </div>`,
						components: {
							TuiGrid: Grid
						},
						data() {
							return {
								options: {}
							};
						},
						created() {
							this.options = makeGridOptions({
								bodyHeight: 320,
								columns: [
									{
										name: 'order',
										header: 'Order',
										align: 'center'
									},
									{
										name: 'sourceFieldPath',
										header: this.$t('label.mapping.source.field.path')
									},
									{
										name: 'targetFieldPath',
										header: this.$t('label.mapping.target.field.path')
									},
									{
										name: 'errorMessage',
										header: this.$t('label.error.info'),
										formatter: ({ value }) => {
											return `t` === value
												? this.$t('label.mapping.target.not.found')
												: this.$t('label.mapping.source.not.found');
										}
									}
								]
							});
						},
						mounted() {
							this.resetData();
						},
						methods: {
							resetData() {
								const errorRuleDataArr = [];

								_this.errorRuleList.forEach(info => {
									errorRuleDataArr.push({
										order: info.order,
										sourceFieldPath: info.mappingExpression ? info.mappingExpression : null,
										targetFieldPath: info.targetFieldPath ? info.targetFieldPath : null,
										errorMessage: info.targetFieldPath || (info.mappingExpression && info.targetFieldPath) ? `t` : `s`,
										_attributes: {
											className: {
												column: {
													sourceFieldPath: `source` === info.errorTarget || `both` === info.errorTarget ? ['red'] : [''],
													targetFieldPath: `target` === info.errorTarget || `both` === info.errorTarget ? ['red'] : ['']
												}
											}
										}
									});
								});

								this.$refs.tuiGrid.invoke(`resetData`, errorRuleDataArr);
							}
						}
					}
				]
			});
		},
		mappingArrowParentWheel(evt) {
			document.querySelectorAll(`#mappingInfo .tui-grid-rside-area .tui-grid-body-area`).forEach(element => {
				element.scrollTop += 40 * (0 < evt.deltaY ? 1 : -1);
			});
		},
		removeSingleMapping() {
			if (0 === this.multiTargetFields.length) {
				this.$alert({ type: `warn`, message: `${this.$t('msg.no.target.selected')}` });
				return;
			}

			for (const dragZoneRowKey of this.multiTargetFields) {
				const id = this.$refs.targetGrid.invoke('getValue', dragZoneRowKey, 'id');

				const ruleDetail = this.$refs.mappingRuleGrid.invoke('getValue', this.mappingRuleGridSelectedRowKey, 'ruleDetail');

				this.$refs.mappingRuleGrid.invoke(
					'setValue',
					this.mappingRuleGridSelectedRowKey,
					'ruleDetail',
					ruleDetail.filter(info => info.targetFieldPath !== id)
				);

				const multiFuncArrow = this.$refs.mappingRuleGrid.invoke(`getValue`, this.mappingRuleGridSelectedRowKey, `multiFuncArrow`);

				if (multiFuncArrow && 0 < multiFuncArrow.length) {
					this.$refs.mappingRuleGrid.invoke(
						'setValue',
						this.mappingRuleGridSelectedRowKey,
						'multiFuncArrow',
						cloneDeep(multiFuncArrow.filter(info => info.targetFieldPath !== id))
					);
				}
			}

			this.delMultiSelect(`target`, true);

			this.drawArrow();
		},
		mappingTitleBtnClick() {
			setTimeout(() => {
				this.mainResize();
			}, 300);
		},
		async getSameIdInfo() {
			if (null === this.mappingRuleGridSelectedRowKey) return;

			const mappingDetail = this.getMappingDetailObject();

			const info = this.$refs.mappingRuleGrid.invoke('getRow', this.mappingRuleGridSelectedRowKey);

			mappingDetail.mappingRuleObject.pk.mappingId = info[`pk.mappingId`];
			mappingDetail.mappingRuleObject.pk.mappingOrder = info[`pk.mappingOrder`];
			mappingDetail.mappingRuleObject.recordId = info['targetRecordObject']['id'];
			mappingDetail.mappingRuleObject.recordObject = info['targetRecordObject'];

			const recordInfo = `I` === this.data.type ? this.data.interfaceRecordInfo : this.data.serviceRecordInfo;

			info.sourceRecordObject.forEach((recordObject, idx) => {
				mappingDetail.mappingRuleObject[`mappingRuleSources`].push({
					pk: {
						mappingId: info[`pk.mappingId`],
						mappingOrder: Number(info[`pk.mappingOrder`]),
						parameterOrder: idx
					},
					recordId: recordObject['id'],
					recordObject: recordInfo.filter(info => info.id === recordObject['id'])[0]
				});
			});

			const result = await new HttpReq(`/api/entity/interface/generateMapping`).create(mappingDetail.mappingRuleObject);

			const mappingRuleDetails = result.data.object.mappingRuleDetails;

			this.$refs.mappingRuleGrid.invoke('setValue', this.mappingRuleGridSelectedRowKey, 'multiFuncArrow', null);

			this.$refs.mappingRuleGrid.invoke(
				'setValue',
				this.mappingRuleGridSelectedRowKey,
				'ruleDetail',
				mappingRuleDetails && 0 < mappingRuleDetails.length
					? mappingRuleDetails.map(mappingRuleDetail => ({
							mappingExpression: mappingRuleDetail.mappingExpression,
							targetFieldPath: mappingRuleDetail.targetFieldPath,
							simpleMappingYn: mappingRuleDetail.simpleMappingYn,
							arraySizeParameter: mappingRuleDetail.arraySizeParameter ? mappingRuleDetail.arraySizeParameter : null
					  }))
					: null
			);

			this.ruleClick(this.mappingRuleGridSelectedRowKey);
		},
		mainResize() {
			setTimeout(() => {
				this.resize();
			}, 250);
		},
		initTreeData(fields, parentId, depth = 1) {
			if (!fields) return;

			fields = cloneDeep(fields);

			const rtnArr = [];

			fields.forEach(field => {
				field['pk.fieldId'] = field.pk.fieldId;
				field['pk.recordId'] = field.pk.recordId;
				field['id'] = `${parentId}\\${field.pk.fieldId}`;

				if ('N' !== field.arrayType) field.id += `[i${depth}]`;

				if (field.options) {
					field.options.forEach(optionInfo => {
						field[optionInfo.pk.keyId] = optionInfo.property;
					});

					delete field.options;
				}

				if (!field._attributes) field._attributes = {};

				if (field.recordObject) {
					field.fieldType = 'R';

					field._attributes.expanded = true;

					field._children = this.initTreeData(field.recordObject.fields, field['id'], 'N' !== field.arrayType ? depth + 1 : depth);
				}

				delete field.pk;
				delete field.options;

				rtnArr.push(field);
			});

			return rtnArr;
		},
		getData() {
			this.$refs.mappingRuleGrid.invoke('blur');
			this.$refs.mappingRuleGrid.invoke('cancelEditing');

			const mappingDetails = [];

			this.$refs.mappingRuleGrid.invoke('getData').forEach(info => {
				const mappingDetail = this.getMappingDetailObject();

				mappingDetail.pk.mappingId = info[`pk.mappingId`];
				mappingDetail.pk.mappingOrder = Number(info[`pk.mappingOrder`]);
				mappingDetail.targetForm = info[`targetForm`];
				mappingDetail.mappingDetailDesc = info[`mappingDetailDesc`];

				mappingDetail.mappingRuleObject.pk.mappingId = info[`pk.mappingId`];
				mappingDetail.mappingRuleObject.pk.mappingOrder = info[`pk.mappingOrder`];
				mappingDetail.mappingRuleObject.recordId = info['targetRecordObject']['id'];

				info.sourceRecordObject.forEach((recordObject, idx) => {
					mappingDetail.mappingRuleObject[`mappingRuleSources`].push({
						pk: {
							mappingId: info[`pk.mappingId`],
							mappingOrder: Number(info[`pk.mappingOrder`]),
							parameterOrder: idx
						},
						recordId: recordObject['id']
					});
				});

				if (info.ruleDetail)
					info.ruleDetail.forEach((detail, idx) => {
						mappingDetail.mappingRuleObject[`mappingRuleDetails`].push({
							pk: {
								mappingId: info[`pk.mappingId`],
								mappingOrder: Number(info[`pk.mappingOrder`]),
								mappingDetailNo: idx
							},
							targetFieldPath: detail.targetFieldPath,
							mappingExpression: detail.mappingExpression,
							simpleMappingYn: detail.simpleMappingYn,
							arraySizeParameter: detail.arraySizeParameter
						});
					});

				mappingDetails.push(mappingDetail);
			});

			return { mappingDetails };
		},
		ruleClick(rowKey) {
			if ('undefined' === typeof rowKey) return;

			this.mappingRuleGridSelectedRowKey = rowKey;

			const dataList = this.$refs.mappingRuleGrid.invoke('getData');

			dataList.forEach(data => {
				this.$refs.mappingRuleGrid.invoke('removeRowClassName', data.rowKey, 'row-selected');
			});

			this.$refs.mappingRuleGrid.invoke('addRowClassName', rowKey, 'row-selected');

			const { sourceRecordObject, targetRecordObject } = this.$refs.mappingRuleGrid.invoke('getRow', rowKey);

			this.$refs.sourceGrid.invoke('clear');
			this.$refs.sourceGrid.invoke(
				'resetData',
				(() => {
					const sourceGridDataArr = [];

					sourceRecordObject.forEach((recordObject, idx) => {
						sourceGridDataArr.push({
							id: `$${idx}`,
							'pk.fieldId': recordObject.id,
							fieldType: 'R',
							_attributes: {
								expanded: true
							},
							_children: this.initTreeData(recordObject.fields, `$${idx}`)
						});
					});

					return sourceGridDataArr;
				})()
			);

			this.$refs.targetGrid.invoke('clear');
			this.$refs.targetGrid.invoke('resetData', [
				{
					id: ``,
					'pk.fieldId': targetRecordObject.id,
					fieldType: 'R',
					_attributes: {
						expanded: true
					},
					_children: this.initTreeData(targetRecordObject.fields, ``)
				}
			]);
		},
		removeRule() {
			this.$refs.mappingRuleGrid.invoke('removeRow', this.mappingRuleGridSelectedRowKey);

			if (this.$refs.mappingRuleGrid.invoke('getRowCount')) {
				this.ruleClick(this.$refs.mappingRuleGrid.invoke('getRowAt', 0).rowKey);
			} else {
				const ctx = this.$refs.mappingArrow.getContext('2d');
				ctx.clearRect(0, 0, ctx.canvas.width, ctx.canvas.height);

				this.$refs.sourceGrid.invoke('clear');
				this.$refs.targetGrid.invoke('clear');

				this.mappingRuleGridSelectedRowKey = null;
			}
		},
		addRule() {
			const _this = this;

			const modalInstance = this.$openModal({
				title: this.$t('label.modelRecord'),
				modalWidth: '600px',
				modalHeight: `330px`,
				contentList: [
					{
						template: `<div><TuiGrid ref="addRuleGrid" v-bind="options" @click="onClick" /></div>`,
						components: {
							TuiGrid: Grid
						},
						data() {
							return {
								options: {
									header: {
										height: 32,
										align: 'center'
									},
									columnOptions: {
										resizable: true
									},
									scrollX: false,
									columns: [
										{
											name: 'recordObject',
											hidden: true
										},
										{
											header: this.$t('label.modelRecord.id'),
											name: 'id',
											escapeHTML: true
										},
										{
											header: this.$t('label.modelRecord.name'),
											name: 'name',
											escapeHTML: true
										},
										{
											header: this.$t('label.privilege.id'),
											name: 'privilegeId',
											escapeHTML: true
										}
									],
									data: []
								}
							};
						},
						created() {
							const recordInfo = `I` === _this.data.type ? _this.data.serviceRecordInfo : _this.data.interfaceRecordInfo;

							recordInfo.forEach(recordObject => {
								this.options.data.push({
									recordObject: recordObject,
									id: recordObject.id,
									name: recordObject.name,
									privilegeId: _this.data.privilegeId
								});
							});
						},
						methods: {
							onClick(evt) {
								if ('undefined' === typeof evt.rowKey) return;

								_this.$refs.mappingRuleGrid.invoke('appendRow', {
									sourceRecordObject: `I` === _this.data.type ? _this.data.interfaceRecordInfo : _this.data.serviceRecordInfo,
									targetRecordObject: evt.instance.getRow(evt.rowKey).recordObject,
									'pk.mappingId': _this.$parent.$refs.basicInfo.getData().mappingId,
									'pk.mappingOrder': _this.$refs.mappingRuleGrid.invoke('getData').length,
									targetForm: null,
									mappingDetailDesc: null
								});

								const mappingRuleGridDataList = _this.$refs.mappingRuleGrid.invoke('getData');

								const mappingRuleGridLastRowKey = mappingRuleGridDataList[mappingRuleGridDataList.length - 1].rowKey;

								_this.ruleClick(mappingRuleGridLastRowKey);

								modalInstance.hideModal();
							}
						}
					}
				]
			});
		},
		resize() {
			this.$nextTick(() => {
				const mappingModal = document.querySelector('#mappingModal');

				const width = mappingModal.clientWidth - 96;

				this.$refs.mappingRuleGridParent.style.width = width;
				this.$refs.mappingRuleGrid.invoke('setWidth', width);

				const sourceGridParentWidth = Math.floor(width * 0.35);
				const targetGridParentWidth = Math.floor(width * 0.35);
				const mappingArrowWidth = width - (sourceGridParentWidth + targetGridParentWidth) - 8;

				this.$refs.sourceGridParent.style.width = `${sourceGridParentWidth}px`;
				this.$refs.sourceGrid.invoke('setWidth', sourceGridParentWidth);

				this.$refs.targetGridParent.style.width = `${targetGridParentWidth}px`;
				this.$refs.targetGrid.invoke('setWidth', targetGridParentWidth);

				this.$refs.mappingArrowParent.style.width = `${mappingArrowWidth}px`;
				this.$refs.mappingArrow.setAttribute('width', mappingArrowWidth);

				if (!this.data.readonly) this.$refs.targetGridBtn.style.marginLeft = `${sourceGridParentWidth - 80 + mappingArrowWidth}px`;

				const modalBodyHeight = document.querySelector(`#mappingModal`).parentNode.parentNode.clientHeight - 32;

				let minusHeight = 32 + 48 + 40 + 40 + 20 + document.querySelector(`#mappingModal #mappingRuleTitle`).offsetHeight;

				if (!this.data.readonly) minusHeight += 32;

				this.style.height = `${modalBodyHeight - minusHeight}px`;

				this.$refs.sourceGrid.invoke('setBodyHeight', modalBodyHeight - minusHeight - 40);
				this.$refs.targetGrid.invoke('setBodyHeight', modalBodyHeight - minusHeight - 40);
				this.$refs.mappingArrow.setAttribute('height', modalBodyHeight - minusHeight);

				this.$refs.sourceGridParent.style.height = `${modalBodyHeight - minusHeight}px`;
				this.$refs.targetGridParent.style.height = `${modalBodyHeight - minusHeight}px`;

				this.drawArrow();
			});
		},
		drawArrow() {
			if (!this.$refs.sourceGrid) return;
			if (!this.$refs.targetGrid) return;

			const sourceGridData = this.$refs.sourceGrid.invoke('getData');
			const targetGridData = this.$refs.targetGrid.invoke('getData');

			const ctx = this.$refs.mappingArrow.getContext('2d');

			ctx.clearRect(0, 0, ctx.canvas.width, ctx.canvas.height);

			const mappingRuleInfo = this.$refs.mappingRuleGrid.invoke('getRow', this.mappingRuleGridSelectedRowKey);

			if (null === mappingRuleInfo || null === mappingRuleInfo.ruleDetail) return;

			const multiFuncArrow = mappingRuleInfo.multiFuncArrow;
			const multiSelectFields = this.multiTargetFields.map(rowKey => this.$refs.targetGrid.invoke(`getRow`, rowKey).id);

			for (let i = 0; i < mappingRuleInfo.ruleDetail.length; i++) {
				const info = mappingRuleInfo.ruleDetail[i];

				let startYPos = 18;
				let startY1Pos = 18;

				let sourceDataInfo = null;
				let targetDataInfo = null;

				const multiStartYPos = [];
				const multiSourceData = [];
				const multiErrorData = [];

				if (multiFuncArrow && 0 < multiFuncArrow.filter(multiFunc => multiFunc.targetFieldPath === info.targetFieldPath).length) {
					const filterMultiFuncArrow = multiFuncArrow.filter(multiFunc => multiFunc.targetFieldPath === info.targetFieldPath);

					filterMultiFuncArrow.forEach(info => {
						if (sourceGridData.find(({ id }) => id === info.mappingExpression)) {
							multiSourceData.push(sourceGridData.find(({ id }) => id === info.mappingExpression));

							let y = 18;

							const groupInfoArr = [];

							for (let i = 0; i < sourceGridData.length; i++) {
								const sourceData = sourceGridData[i];

								if (`R` === sourceData.fieldType) {
									if (null === sourceData._attributes.tree.parentRowKey) {
										y += 40;
									} else {
										if (!sourceData.id.startsWith(groupInfoArr[groupInfoArr.length - 1].id)) {
											groupInfoArr.splice(groupInfoArr.length - 1, 1);
										}

										if (groupInfoArr[groupInfoArr.length - 1].expanded) y += 40;
									}

									groupInfoArr.push({ id: sourceData.id, expanded: sourceData._attributes.expanded });
								} else {
									if (!sourceData.id.startsWith(groupInfoArr[groupInfoArr.length - 1].id)) {
										groupInfoArr.splice(groupInfoArr.length - 1, 1);
									}

									if (groupInfoArr[groupInfoArr.length - 1].expanded) y += 40;
								}

								if (info.mappingExpression == sourceData.id) {
									break;
								}
							}

							multiStartYPos.push(y);
						} else {
							multiErrorData.push(info.mappingExpression);
						}
					});
				} else {
					for (let i = 0; i < sourceGridData.length; i++) {
						let data = sourceGridData[i];

						if (data.id == info.mappingExpression) {
							sourceDataInfo = data;

							const groupInfoArr = [];

							for (let i = 0; i < sourceGridData.length; i++) {
								const sourceData = sourceGridData[i];

								if (`R` === sourceData.fieldType) {
									if (null === sourceData._attributes.tree.parentRowKey) {
										startYPos += 40;
									} else {
										if (!sourceData.id.startsWith(groupInfoArr[groupInfoArr.length - 1].id)) {
											groupInfoArr.splice(groupInfoArr.length - 1, 1);
										}

										if (groupInfoArr[groupInfoArr.length - 1].expanded) startYPos += 40;
									}

									groupInfoArr.push({ id: sourceData.id, expanded: sourceData._attributes.expanded });
								} else {
									if (!sourceData.id.startsWith(groupInfoArr[groupInfoArr.length - 1].id)) {
										groupInfoArr.splice(groupInfoArr.length - 1, 1);
									}

									if (groupInfoArr[groupInfoArr.length - 1].expanded) startYPos += 40;
								}

								if (data.id == sourceData.id) {
									break;
								}
							}

							break;
						}
					}
				}

				for (let i = 0; i < targetGridData.length; i++) {
					let data = targetGridData[i];

					if (data.id == info.targetFieldPath) {
						targetDataInfo = data;

						const groupInfoArr = [];

						for (let i = 0; i < targetGridData.length; i++) {
							const targetData = targetGridData[i];

							if (`R` === targetData.fieldType) {
								if (null === targetData._attributes.tree.parentRowKey) {
									startY1Pos += 40;
								} else {
									if (!targetData.id.startsWith(groupInfoArr[groupInfoArr.length - 1].id)) {
										groupInfoArr.splice(groupInfoArr.length - 1, 1);
									}

									if (groupInfoArr[groupInfoArr.length - 1].expanded) startY1Pos += 40;
								}

								groupInfoArr.push({ id: targetData.id, expanded: targetData._attributes.expanded });
							} else {
								if (!targetData.id.startsWith(groupInfoArr[groupInfoArr.length - 1].id)) {
									groupInfoArr.splice(groupInfoArr.length - 1, 1);
								}

								if (groupInfoArr[groupInfoArr.length - 1].expanded) startY1Pos += 40;
							}

							if (data.id == targetData.id) {
								break;
							}
						}

						break;
					}
				}

				const drawStartYPos = startYPos - this.sourceScrollTop > 24 ? startYPos - this.sourceScrollTop : 24;
				const drawStartY1Pos = startY1Pos - this.targetScrollTop > 24 ? startY1Pos - this.targetScrollTop : 24;

				let isSelected = -1 < multiSelectFields.indexOf(info.targetFieldPath);
				let isValidation = true;

				if (0 < multiErrorData.length || 0 < multiSourceData.length) {
					for (let i = 0; i < multiErrorData.length; i++) {
						this.drawLineError(ctx, drawStartY1Pos, isSelected);
					}

					for (let i = 0; i < multiSourceData.length; i++) {
						const drawYpos = multiStartYPos[i] - this.sourceScrollTop > 24 ? multiStartYPos[i] - this.sourceScrollTop : 24;

						this.drawLineFunc(ctx, drawYpos, drawStartY1Pos, isSelected);
					}

					this.drawLineTarget(ctx, drawStartY1Pos, isSelected);
				} else {
					if (!sourceDataInfo) {
						if (`N` === info.simpleMappingYn || (info.mappingExpression && !info.mappingExpression.startsWith(`$`)))
							this.drawLineTarget(ctx, drawStartY1Pos, isSelected);
						else if (targetDataInfo) this.drawLineError(ctx, drawStartY1Pos, isSelected);
					}

					if (sourceDataInfo && targetDataInfo) {
						if (
							sourceDataInfo.fieldType !== targetDataInfo.fieldType ||
							(0 !== sourceDataInfo.fieldLength &&
								0 !== targetDataInfo.fieldLength &&
								sourceDataInfo.fieldLength !== targetDataInfo.fieldLength)
						)
							isValidation = false;

						this.drawLineBoth(ctx, drawStartYPos, drawStartY1Pos, isSelected, isValidation);
					}
				}
			}
		},
		drawLineBoth(ctx, startYPos, startY1Pos, isSelected, isValidation) {
			let lineColor = null;

			if (isValidation) lineColor = isSelected ? '#212F39' : `#90969B`;
			else lineColor = isSelected ? 'rgba(237, 49, 49, 1)' : `rgba(237, 49, 49, 0.8)`;

			ctx.save();

			if (isSelected) ctx.lineWidth = 5;

			ctx.beginPath();

			ctx.moveTo(0, startYPos);
			ctx.lineTo(50, startYPos);
			ctx.lineTo(ctx.canvas.width - 50, startY1Pos);
			ctx.lineTo(ctx.canvas.width, startY1Pos);
			ctx.strokeStyle = lineColor;
			ctx.stroke();
			ctx.closePath();

			ctx.beginPath();
			ctx.moveTo(0, startYPos - 5);
			ctx.lineTo(0, startYPos + 5);
			ctx.lineTo(10, startYPos + 5);
			ctx.lineTo(10, startYPos - 5);
			ctx.lineTo(0, startYPos - 5);
			ctx.fillStyle = lineColor;
			ctx.fill();
			ctx.closePath();

			ctx.beginPath();
			ctx.moveTo(ctx.canvas.width, startY1Pos);
			ctx.lineTo(ctx.canvas.width - 8, startY1Pos - 8);
			ctx.lineTo(ctx.canvas.width - 8, startY1Pos + 8);
			ctx.lineTo(ctx.canvas.width, startY1Pos);
			ctx.fillStyle = lineColor;
			ctx.fill();
			ctx.closePath();

			ctx.restore();
		},
		drawLineTarget(ctx, startY1Pos, isSelected) {
			let lineColor = isSelected ? '#212F39' : `#90969B`;

			ctx.save();

			if (isSelected) ctx.lineWidth = 5;

			ctx.beginPath();
			ctx.fillStyle = lineColor;
			ctx.fillText('|F|', ctx.canvas.width - ctx.canvas.width / 3 - 14, startY1Pos + 3);
			ctx.closePath();

			ctx.beginPath();
			ctx.moveTo(ctx.canvas.width - ctx.canvas.width / 3, startY1Pos);
			ctx.lineTo(ctx.canvas.width - 8, startY1Pos);
			ctx.save();
			ctx.setLineDash([5, 5]);
			ctx.strokeStyle = lineColor;
			ctx.stroke();
			ctx.restore();
			ctx.closePath();

			ctx.beginPath();
			ctx.moveTo(ctx.canvas.width, startY1Pos);
			ctx.lineTo(ctx.canvas.width - 8, startY1Pos - 8);
			ctx.lineTo(ctx.canvas.width - 8, startY1Pos + 8);
			ctx.lineTo(ctx.canvas.width, startY1Pos);
			ctx.fillStyle = lineColor;
			ctx.fill();
			ctx.closePath();

			ctx.restore();
		},
		drawLineFunc(ctx, startYPos, startY1Pos, isSelected) {
			let lineColor = isSelected ? '#212F39' : `#90969B`;

			ctx.save();

			if (isSelected) ctx.lineWidth = 5;

			ctx.beginPath();
			ctx.moveTo(0, startYPos);
			ctx.lineTo(ctx.canvas.width - ctx.canvas.width / 3 - 14, startY1Pos);
			ctx.save();
			ctx.setLineDash([5, 5]);
			ctx.strokeStyle = lineColor;
			ctx.stroke();
			ctx.restore();
			ctx.closePath();

			ctx.beginPath();
			ctx.moveTo(0, startYPos - 5);
			ctx.lineTo(0, startYPos + 5);
			ctx.lineTo(10, startYPos + 5);
			ctx.lineTo(10, startYPos - 5);
			ctx.lineTo(0, startYPos - 5);
			ctx.fillStyle = lineColor;
			ctx.fill();
			ctx.closePath();

			ctx.restore();
		},
		drawLineError(ctx, startY1Pos, isSelected) {
			let lineColor = isSelected ? '#6a070a' : `#c1282e`;

			ctx.save();

			if (isSelected) ctx.lineWidth = 5;

			ctx.beginPath();
			ctx.moveTo(10, startY1Pos - 7.5);
			ctx.lineTo(25, startY1Pos + 7.5);
			ctx.moveTo(10, startY1Pos + 7.5);
			ctx.lineTo(25, startY1Pos - 7.5);
			ctx.strokeStyle = lineColor;
			ctx.stroke();
			ctx.closePath();

			ctx.moveTo(40, startY1Pos);
			ctx.lineTo(ctx.canvas.width - 8, startY1Pos);
			ctx.strokeStyle = lineColor;
			ctx.stroke();
			ctx.closePath();

			ctx.beginPath();
			ctx.moveTo(ctx.canvas.width, startY1Pos);
			ctx.lineTo(ctx.canvas.width - 8, startY1Pos - 8);
			ctx.lineTo(ctx.canvas.width - 8, startY1Pos + 8);
			ctx.lineTo(ctx.canvas.width, startY1Pos);
			ctx.fillStyle = lineColor;
			ctx.fill();
			ctx.closePath();

			ctx.restore();
		},
		initDragDropEvt() {
			if ($(this.$refs.sourceGridParent).draggable()) {
				$(this.$refs.sourceGridParent).draggable('destroy');
			}

			if ($(this.$refs.targetGridParent).find('td').droppable()) {
				$(this.$refs.targetGridParent).find('td').droppable('destroy');
			}

			if (this.data.readonly) return;

			$(this.$refs.sourceGridParent).draggable({
				revert: true,
				revertDuration: 0,
				helper: () => `<span></span>`,
				start: () => {
					let dragZoneRowKey;

					if (0 === this.multiSourceFields.length) {
						dragZoneRowKey = this.$refs.sourceGrid.invoke('getFocusedCell').rowKey;

						if (!dragZoneRowKey) return false;

						if ('R' === this.$refs.sourceGrid.invoke('getRow', dragZoneRowKey).fieldType) return false;
					} else {
						this.multiSourceFields = this.multiSourceFields
							.filter(rowKey => {
								const isGroup = 'R' === this.$refs.sourceGrid.invoke('getRow', rowKey).fieldType;

								if (isGroup) {
									this.$refs.sourceGrid.invoke('removeRowClassName', rowKey, 'gray');
								}

								return !isGroup;
							})
							.sort((a, b) => a - b);

						if (0 === this.multiSourceFields.length) return false;

						dragZoneRowKey = this.multiSourceFields[0];
						this.$refs.sourceGrid.invoke('focus', this.multiSourceFields[0]);
					}

					this.dragger = document.createElement('div');
					this.dragger.classList.add('dragger');

					const fieldName = this.$refs.sourceGrid.invoke('getRow', dragZoneRowKey).fieldName;

					if (1 >= this.multiSourceFields.length) {
						this.dragger.innerText = fieldName;
					} else {
						this.dragger.innerText = this.$t(`msg.mapping.other.than`, { 0: fieldName, 1: this.multiSourceFields.length - 1 });
					}

					document.body.appendChild(this.dragger);

					document.body.style.cursor = `move`;
				},
				drag: evt => {
					const dragZoneRowKey =
						0 === this.multiSourceFields.length ? this.$refs.sourceGrid.invoke('getFocusedCell').rowKey : this.multiSourceFields[0];

					const dragRow = this.$refs.sourceGrid.invoke('getRow', dragZoneRowKey);

					if (dragRow && 'R' === dragRow.fieldType) return false;

					this.dragger.style.top = `${evt.pageY}px`;
					this.dragger.style.left = `${evt.pageX + 15}px`;
				},
				stop: () => {
					document.body.removeChild(this.dragger);
					document.body.style.cursor = `auto`;
				}
			});

			$(this.$refs.targetGridParent).droppable({
				tolerance: 'pointer',
				drop: evt => {
					let dragZoneRowKey = null;
					let dropZoneRowKey = null;
					const dropInfoList = [];

					let ruleDetail = this.$refs.mappingRuleGrid.invoke('getValue', this.mappingRuleGridSelectedRowKey, 'ruleDetail');

					if (0 === this.multiSourceFields.length) {
						// no focus cell && single
						dragZoneRowKey = this.$refs.sourceGrid.invoke('getFocusedCell').rowKey;
						dropZoneRowKey = getDropZoneRowKey();

						if (!dropZoneRowKey) return;

						if ('R' === this.$refs.targetGrid.invoke('getRow', dropZoneRowKey).fieldType) {
							this.$alert({ type: 'warn', message: this.$t('msg.mapping.not.add.group.field') });
							return;
						}

						const isOverride =
							ruleDetail &&
							ruleDetail.find(rule => rule.targetFieldPath === this.$refs.targetGrid.invoke('getValue', dropZoneRowKey, 'id'));

						dropInfoList.push({ isOverride, dragZoneRowKey, dropZoneRowKey });
					} else {
						// focus cell && (single or multi)
						for (let idx = 0; idx < this.multiSourceFields.length; idx++) {
							dragZoneRowKey = this.multiSourceFields[idx];

							if (0 === idx) {
								dropZoneRowKey = getDropZoneRowKey();

								if (!dropZoneRowKey) return;
							} else {
								let row;

								do {
									dropZoneRowKey++;
									row = this.$refs.targetGrid.invoke('getRow', dropZoneRowKey);
								} while (row && row.fieldType === 'R');
							}

							if (!dropZoneRowKey || dropZoneRowKey > this.$refs.targetGrid.invoke(`getRowCount`) - 1) continue;

							if ('R' === this.$refs.targetGrid.invoke('getRow', dropZoneRowKey).fieldType) {
								this.$alert({ type: 'warn', message: this.$t('msg.mapping.not.add.group.field') });
								break;
							}

							const isOverride =
								ruleDetail &&
								ruleDetail.find(rule => rule.targetFieldPath === this.$refs.targetGrid.invoke('getValue', dropZoneRowKey, 'id'));

							dropInfoList.push({ isOverride, dragZoneRowKey, dropZoneRowKey });
						}
					}

					if (dropInfoList.some(({ isOverride }) => isOverride)) {
						this.$confirm({
							type: 'warn',
							message: this.$t(`msg.mapping.override`),
							callback: () => {
								dropInfoList.forEach(dropInfo => {
									ruleDetail = this.$refs.mappingRuleGrid.invoke('getValue', this.mappingRuleGridSelectedRowKey, 'ruleDetail');

									this.$refs.mappingRuleGrid.invoke(
										'setValue',
										this.mappingRuleGridSelectedRowKey,
										'ruleDetail',
										ruleDetail
											.filter(
												rule =>
													rule.targetFieldPath !== this.$refs.targetGrid.invoke('getValue', dropInfo.dropZoneRowKey, 'id')
											)
											.map(info => ({
												targetFieldPath: info.targetFieldPath,
												mappingExpression: info.mappingExpression,
												simpleMappingYn: info.simpleMappingYn,
												arraySizeParameter: info.arraySizeParameter
											}))
									);

									makeRuleDetail.call(this, dropInfo);
								});

								this.delMultiSelect(`source`, true);
							}
						});
					} else {
						dropInfoList.forEach(dropInfo => {
							makeRuleDetail.call(this, dropInfo);
						});

						this.delMultiSelect(`source`, true);
					}

					function getDropZoneRowKey() {
						let targetElement = evt.originalEvent.target;

						while (null == dropZoneRowKey && targetElement !== evt.target) {
							if (targetElement.getAttribute('data-row-key')) {
								return Number(targetElement.getAttribute('data-row-key'));
							} else {
								targetElement = targetElement.parentElement;
							}
						}
					}

					function makeRuleDetail({ dragZoneRowKey, dropZoneRowKey }) {
						if (0 < this.$refs.targetGrid.invoke('getRow', dropZoneRowKey)._children.length) return false;

						const targetFieldPath = this.$refs.targetGrid.invoke('getRow', dropZoneRowKey).id;
						const mappingExpression = this.$refs.sourceGrid.invoke('getRow', dragZoneRowKey).id;
						const arraySizeParameter = (() => {
							let arrayReference = null;

							const mappingExpressionArr = mappingExpression.split(`\\`);

							if (0 < mappingExpressionArr.filter(expression => 1 < expression.split(`[i`).length).length) {
								const arraySizeParameterArr = [];

								mappingExpressionArr.forEach(expression => {
									arraySizeParameterArr.push(1 < expression.split(`[i`).length ? `${expression.split(`[i`)[0]}[]` : expression);
								});

								arraySizeParameterArr.pop();

								arrayReference = arraySizeParameterArr.join(`\\`);
							}

							return arrayReference;
						})();

						const mappingRuleInfo = this.$refs.mappingRuleGrid.invoke('getRow', this.mappingRuleGridSelectedRowKey);

						let newRuleDetail = null;

						if (null === mappingRuleInfo.ruleDetail) {
							newRuleDetail = [
								{
									targetFieldPath,
									mappingExpression,
									arraySizeParameter,
									simpleMappingYn: 'Y'
								}
							];
						} else {
							mappingRuleInfo.ruleDetail.push({
								targetFieldPath,
								mappingExpression,
								arraySizeParameter,
								simpleMappingYn: 'Y'
							});

							newRuleDetail = mappingRuleInfo.ruleDetail.map(info => ({
								targetFieldPath: info.targetFieldPath,
								mappingExpression: info.mappingExpression,
								arraySizeParameter: info.arraySizeParameter,
								simpleMappingYn: info.simpleMappingYn
							}));
						}

						this.$refs.mappingRuleGrid.invoke('setValue', this.mappingRuleGridSelectedRowKey, 'ruleDetail', newRuleDetail);

						const multiFuncArrow = this.$refs.mappingRuleGrid.invoke(`getValue`, this.mappingRuleGridSelectedRowKey, `multiFuncArrow`);

						if (multiFuncArrow && 0 < multiFuncArrow.length) {
							this.$refs.mappingRuleGrid.invoke(
								'setValue',
								this.mappingRuleGridSelectedRowKey,
								'multiFuncArrow',
								cloneDeep(multiFuncArrow.filter(info => info.targetFieldPath !== targetFieldPath))
							);
						}

						this.resize();
					}
				}
			});
		},
		getMappingDetailObject() {
			return {
				pk: {
					mappingId: null,
					mappingOrder: null
				},
				targetForm: null,
				mappingDetailDesc: null,
				mappingRuleObject: {
					pk: {
						mappingId: null,
						mappingOrder: null
					},
					recordId: null,
					mappingRuleSources: [],
					mappingRuleDetails: []
				}
			};
		},
		initMappingInfo() {
			this.$refs.mappingRuleGrid.invoke('setValue', this.mappingRuleGridSelectedRowKey, 'multiFuncArrow', null);
			this.$refs.mappingRuleGrid.invoke('setValue', this.mappingRuleGridSelectedRowKey, 'ruleDetail', null);
			this.drawArrow();
		},
		async initMultiFuncArrowAll() {
			const list = this.$refs.mappingRuleGrid.invoke(`getData`);

			if (0 === list.length) return;

			const promiseList = [];

			list.forEach(({ rowKey }) => {
				promiseList.push(this.initMultiFuncArrow(rowKey));
			});

			await Promise.all(promiseList);
		},
		async initMultiFuncArrow(rowKey) {
			const ruleDetail = this.$refs.mappingRuleGrid.invoke(`getRow`, rowKey).ruleDetail;

			if (!ruleDetail || 0 === ruleDetail.length) return;

			const promiseList = [];

			ruleDetail.forEach(data => {
				if (`N` !== data.simpleMappingYn) return;

				promiseList.push(
					new Promise(resolve => {
						(async () => {
							resolve({
								res: await new HttpReq(`/api/mapping/expression/sources`).read({ expression: data.mappingExpression }),
								targetFieldPath: data.targetFieldPath
							});
						})();
					})
				);
			});

			const multiFuncArrow = [];

			(await Promise.all(promiseList)).forEach(({ res, targetFieldPath }) => {
				if (`ok` !== res.data.result) return;

				res.data.object.args.forEach(arg => {
					if (`Record` !== arg.expressionType) return;

					multiFuncArrow.push({ mappingExpression: arg.expressionString, targetFieldPath });
				});
			});

			this.$refs.mappingRuleGrid.invoke(`setValue`, rowKey, `multiFuncArrow`, multiFuncArrow);
		},
		extractionFieldId(idList, fields, parentId, depth = 1) {
			if (!fields) return;

			fields = cloneDeep(fields);

			fields.forEach(field => {
				field.id = `${parentId}\\${field.pk.fieldId}`;

				if ('N' !== field.arrayType) field.id += `[i${depth}]`;

				if (field.recordObject) {
					this.extractionFieldId(idList, field.recordObject.fields, field.id, 'N' !== field.arrayType ? depth + 1 : depth);
				}

				idList.push(field.id);
			});
		},
		clickMappingField(evt, type) {
			const {
				rowKey,
				instance,
				nativeEvent: { ctrlKey, shiftKey }
			} = evt;

			const key = `source` === type ? `multiSourceFields` : `multiTargetFields`;

			if (shiftKey) {
				if (0 === this[key].length) {
					this[key].push(rowKey);
					instance.addRowClassName(rowKey, 'gray');
				} else {
					let start, end;
					const lastSelectedIdx = this[key][this[key].length - 1];

					if (this.isShiftKey) {
						start = Math.min(this[key][0], rowKey);
						end = Math.max(lastSelectedIdx, rowKey);
					} else {
						const between = [rowKey, lastSelectedIdx].sort((a, b) => a - b);
						[start, end] = between;
					}

					this.delMultiSelect(type);

					for (let idx = start; idx <= end; idx++) {
						this[key].push(idx);
						instance.addRowClassName(idx, 'gray');
					}
				}
			} else {
				if (!ctrlKey) {
					this.delMultiSelect(type);
				}

				if (0 === this[key].length) {
					this[key].push(rowKey);
				} else {
					if (1 === this[key].length) {
						instance.addRowClassName(this[key][0], 'gray');
					}

					this[key].push(rowKey);
					instance.addRowClassName(rowKey, 'gray');
				}
			}

			this.isShiftKey = shiftKey;

			this.drawArrow();
		},
		delMultiSelect(type, isDelAll = false) {
			const multiFields = `source` === type ? `multiSourceFields` : `multiTargetFields`;
			const gridName = `source` === type ? `sourceGrid` : `targetGrid`;

			this[multiFields].forEach(idx => this.$refs[gridName].invoke(`removeRowClassName`, idx, `gray`));
			this[multiFields] = [];

			if (!isDelAll) return;

			this.$refs[gridName].invoke(`blur`);
			this.$refs[gridName].invoke(`cancelEditing`);

			this.drawArrow();
		},
		gridFocusOut() {
			if (!this.$refs.mappingRuleGrid) return;

			this.$refs.mappingRuleGrid.invoke('blur');
			this.$refs.mappingRuleGrid.invoke('cancelEditing');

			this.delMultiSelect(`source`, true);
			this.delMultiSelect(`target`, true);

			this.isShiftKey = false;
		}
	},
	destroyed() {
		WindowResizeEventBus.$off('MainResize', this.mainResize);
		SideMenuToggleEventBus.$off('MainResize', this.mainResize);
	}
};
</script>

<style lang="scss">
.dragger {
	z-index: 9999;
	position: absolute;
	min-width: 80px;
	min-height: 35px;
	line-height: 35px;
	text-align: center;
	background-color: #484f70;
	color: #fff;
	border-radius: 4px;
}

#mappingInfo {
	.table-responsive {
		display: inline-block;

		.tui-grid-layer-selection {
			display: none;
		}

		.tui-grid-cell-current-row {
			td {
				background: #f5f6fb;
			}
		}
	}
}
</style>
